<style lang="scss" src="./design.scss"></style>

<template>
  <!-- <m-keyup> -->
  <div class="mb-design">
    <div class="layout-header">
      <SpToolBar />
    </div>
    <div class="layout-body">
      <div class="left-silder">
        <SpWidgetsPanel />
      </div>
      <div class="container">
        <!-- <div class="design-container"> -->
        <SpViewPort />
        <!-- </div> -->
      </div>
      <div class="right-silder">
        <SpStylePanel />
      </div>
    </div>
  </div>
  <!-- </m-keyup> -->
</template>

<script>
import { mapMutations, mapState } from 'vuex'
import S from '@/spx'
import { log, isObject } from '@/utils'
// import mKeyup from '@/components/mKeyUp'
import { widgets } from '@/espier-widgets'
import actions from '@/utils/actions'

export default {
  data () {
    return {}
  },
  computed: {
    ...mapState({
      pageWidth: (state) => state.pageWidth
    })
  },
  mounted () {
    if (window.__POWERED_BY_QIANKUN__) {
      actions.onGlobalStateChange((state) => {
        log.debug('[design vue] actions onglobal state change: ', state)
        S.trigger('SYS_CONFIG', state)
        this.getShopDesign()
      }, true)
    } else {
      this.getShopDesign()
    }
  },
  components: {
    // mKeyup
  },
  methods: {
    ...mapMutations(['SET_PAGEDATA', 'ADD_WIDGET']),
    async getShopDesign () {
      let pageid
      if (S['SYS_CONFIG']) {
        pageid = S['SYS_CONFIG'].pageid
      } else {
        pageid = this.$route.query.id
      }

      // console.log(`sys_config:`, S['SYS_CONFIG'])

      this.$Loading.start()
      const header = await this.$api.temp.getHeaderOrFooter({
        page_name: 'header'
      })

      const pbody = await this.$api.temp.getTemplateContent({
        theme_pc_template_id: pageid
      })

      const footer = await this.$api.temp.getHeaderOrFooter({
        page_name: 'footer'
      })

      let wgtList = []
      if (typeof header.params != 'undefined') {
        wgtList = wgtList.concat(JSON.parse(header.params))
      }
      this.$Loading.finish()

      pbody.forEach((item) => {
        wgtList.push(JSON.parse(item.config))
      })

      if (typeof footer.params != 'undefined') {
        wgtList = wgtList.concat(JSON.parse(footer.params))
      }

      // const { list } = await this.$api.temp.getPageData({
      //   template_name: 'pc',
      //   version: 'v1.0.1',
      //   page_name: 'index2'
      // })
      let pagedata = {
        name: '',
        nav: [],
        title: '',
        pageid: pageid,
        widgets: []
      }
      wgtList.forEach((item) => {
        // 合并默认挂件参数
        const { type: widgetName } = item
        const defaultConfig = widgets[widgetName].setting
        let widgetConfig = {
          ...defaultConfig,
          ...item
        }

        if (widgetConfig.childWidgets) {
          const childConfig = widgetConfig.childWidgets.map((citem) => {
            const { type: cWidgetName } = citem
            const defaultConfigChild = widgets[cWidgetName].setting
            return {
              ...defaultConfigChild,
              ...citem,
              leaf: true
            }
          })
          widgetConfig.childWidgets = childConfig
        }
        pagedata.widgets.push(widgetConfig)
      })

      try {
        this.SET_PAGEDATA(pagedata)

        // 查找页面配置
        const wgtPage = wgtList.find((item) => item.type == 'W0000')
        if (!wgtPage) {
          // pagedata.widgets.push(Widgets['W0000'].setting)
          // Widgets['WgtPage'].setting.width = this.pageWidth
          this.ADD_WIDGET(widgets['W0000'])
        }
        S.handleCommit('PAGE_SETUP')
      } catch (e) {
        console.error(`[sp-viewport] getPageData error: ${e}`)
      }
    }
  }
}
</script>
